import axios from 'axios'
import qs from 'qs'

//axios
let axiosIns = axios.create({});

//baseUrl
if (process.env.NODE_ENV == 'development') {

    axiosIns.defaults.baseURL = 'https://app2c.cm.yhcs.com/';

} else if (process.env.NODE_ENV == 'production') {

    axiosIns.defaults.baseURL = 'https://app2c.cm.yhcs.com/';

}

//
axiosIns.defaults.timeout = 5000;

//
axiosIns.defaults.responseType = 'json';

//
axiosIns.defaults.transformRequest = [ (data) => {

        return qs.stringify(data);

    }
];

axiosIns.defaults.validateStatus = (status) => {

    return true;

};

//
axiosIns.interceptors.request.use( (config) => {

    //config
    config.headers.Accept = 'application/json';

    return config;

});

//
axiosIns.interceptors.response.use( (response) => {

    let data = response.data;

    let status = response.status;

    if (status === 200) {

        return Promise.resolve(data);

    } else {

        return Promise.reject(response);

    }
});

//
let ajaxMethod = ['get', 'post'];

let api = {};

ajaxMethod.forEach((method)=> {
    //
    api[method] = function (url, data, config) {

        return new Promise(function (resolve, reject) {

            axiosIns[method](url, data, config).then((response)=> {

                resolve(response);

            }).catch((response)=> {

                reject(response);

            })
        })
    }
});

export default {
    //
    getCity: () => {
        return api.post('city/getMarketCity');
    },
    //
    getArtical: (page, rows) => {
        return api.get('article/getArticlePageList', {
            page: page,
            rows: rows
        });
    },
    //
    add: (token, vehicle_id) => {
        return api.post('watchList/add', {
            token: token,
            vehicleId: vehicle_id
        });
    },
    //
    remove: (token, vehicle_id) => {
        return api.post('watchList/remove', {
            toekn: token,
            vehicleId: vehicle_id
        });
    },
    //
    getBrand: (city) => {
        return api.post('stats/brandsCount', {
            city: city
        });
    },
    //
    getSlideList: (cityId) => {
        return api.post('slideshow/getSlideshowBOList',{
            cityId: cityId
        });
    },
    //
    getRecommend: (token, locationCity, vehicleIds) => {
        return api.post('slideshow/getSlideshowBOList', {
            token: token,
            locationCity: locationCity,
            vehicleIds: vehicleIds
        });
    },
    //
    getFaqPageList: (page, rows) => {
        return api.post('faq/getFaqPageList', {
            page: page,
            rows: rows
        });
    },
    //

}



